<template>
  <div class="">
    <a-table :columns="columns" :data-source="reviewsListData?.reviews" :pagination="pagination">
      <template #bodyCell="{column,record}">
        <template v-if="column?.key==='avatar'">
          <a-avatar :src="record?.client?.avatarUrl?record.client.avatarUrl:require('@/assets/avatar.png') "
                    size="large"></a-avatar>
        </template>
      </template>
    </a-table>
  </div>
</template>


<script>
import {defineComponent} from 'vue'

export default defineComponent({
  name: "ReviewsTable",
  props: ['translationId'],
  watch: {
    'reviewPagination.page'() {
      this.fetchStreamReviews();
    }
  },
  computed: {
    reviewsListData() {
      return this.$store.getters.reviewsListData;
    },
    pagination() {
      return {
        current: this.reviewPagination.page,
        defaultPageSize: this.reviewPagination.limit,
        total: this.reviewsListData?.total,
        onChange: this.loadMoreReviews
      }
    }
  },
  data() {
    return {
      reviewPagination: {
        page: 1,
        limit: 10,
        canLoadMore: true
      },

      columns: [
        {
          title: 'Аватар',
          dataIndex: ['client', 'avatarUrl'],
          key: 'avatar',
          width: 100
        },
        {
          title: 'ФИО',
          ellipsis: true,
          dataIndex: ['client', 'name']
        },
        {
          title: 'Оценка',
          dataIndex: 'estimation',
          width: 100

        },
        {
          title: 'Отзыв',
          ellipsis: true,
          dataIndex: 'body'
        },
      ]
    }
  },
  methods: {
    fetchStreamReviews() {
      this.$store.dispatch('fetchStreamReviews', {
        translationId: this.translationId,
        page: this.reviewPagination.page,
        limit: this.reviewPagination.limit
      });
    },
    loadMoreReviews(page) {
      if (!this.reviewPagination.canLoadMore) return;
      this.reviewPagination.page = page;
    }
  },

  beforeMount() {
    this.fetchStreamReviews();
  },
  beforeUnmount() {
    this.$store.commit('clearReviewsList');
  }
})
</script>


