<template>
  <a-list :grid="{ gutter: 10, xs: 1, sm: 2, md: 2, lg: 2, xl: 2, xxl: 2, xxxl: 2 }" size="small"
    :data-source="quizResults">
    <template #renderItem="{ item }">
      <a-list-item>
        <SurveyActive :cannot-close="true" :quiz-list="[item]"></SurveyActive>
      </a-list-item>
    </template>
  </a-list>
</template>

<script>
import { defineComponent } from 'vue'
import SurveyActive from "@/components/stream/SurveyActive.vue";

export default defineComponent({
  name: "QuizResults",
  components: { SurveyActive },
  props: ['translationId'],
  data() {
    return {
      quizResults: []
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchQuizResultsList', this.translationId).then((results) => {
      this.quizResults = results.map(item => {
        item.answers = JSON.parse(item.answers);
        return item;
      });
    })
  }
})
</script>


<style scoped>
.quiz-container {
  display: flex;
  flex-direction: column;
}
</style>