export function convertTimeInMinuteAndSeconds(time) {
    let hours = Math.floor(time / 60 / 60);

    let minutes = Math.floor(time / 60) - (hours * 60);

    let seconds = time % 60;

    return (hours < 10 ? '0' + hours : hours) + ':' + (minutes < 10 ? '0' + minutes : minutes) + ':' + (seconds < 10 ? '0' + seconds : seconds);
}

export function ageCalculation(birth) {
    if (!birth || birth === 'null') return 'Не известно';
    let years = Math.abs(
        new Date(
            Date.now() - new Date(birth).getTime() // 1. Get the difference as new Date object

        ).getUTCFullYear() - 1970); // 2. Calculate the years

    const residue = years % 10;

    if (residue === 1) years += ' год';
    else if (residue > 1 && residue < 5) years += ' года';
    else years += ' лет';

    return years
}


export function convertFileSize(bytes) {
    const KB = (bytes / 1000).toFixed(1);
    if (KB >= 1000) {
        const MB = (KB / 1000).toFixed(1);
        if (MB >= 1000) {
            const GB = (MB / 1000).toFixed(1);
            return `${GB} Гб`
        }
        return `${MB} Мб`;
    }
    return `${KB} Кб`;
}