<template>
  <div class="survey-wrapper">
    <div class="survey">
      <div class="caption">
        <p class="title">{{
            quizData?.title
          }}</p>
        <button v-if="!cannotClose" class="close" @click="closeQuiz"></button>
      </div>
      <div class="list list-finish" v-if="quizData?.results">
        <div class="element" v-for="item in quizData?.answers" :key="item?.id">
          <p class="percentage">
            {{ getAnswerResult(item.order) }}%
          </p>
          <div class="element-filling">
            <p class="element-text">{{ item?.body }}</p>
            <div class="element-line" :style="{ width: `${getAnswerResult(item.order)}%` }"></div>
          </div>
        </div>

      </div>

      <div class="list list-select" v-else>
        <button @click="passingQuiz(item?.order)" class="element" v-for="item in quizData?.answers" :key="item?.order">
          <div class="element-circle"></div>
          {{ item?.body }}
        </button>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "SurveyActive",
  computed: {
    allAnswers() {
      if (this.quizData?.results) {
        return this.quizData?.results?.map(item => item?._count?.id).reduce((acc, curr) => acc + curr);
      }
      return 0
    },
    quizData() {
      return this.quizList?.length ? this.quizList?.[this.quizList?.length - 1] : null;
    }
  },
  methods: {
    getAnswerResult(order) {
      const index = this.quizData?.results?.map(item => item?.answerOrder).indexOf(order);
      if (index > -1) {
        return Math.floor((100 / this.allAnswers) * this.quizData?.results?.[index]?._count?.id);
      }
      return 0;
    },
    passingQuiz(answerOrder) {
      this.$emit('passingQuiz', {quizId: this.quizData?.id, answerOrder});
    },
    closeQuiz() {
      this.$emit('closeQuiz');
    }
  },
  props: ['quizList','cannotClose'],
}
</script>

