<template>
  <div class="survey-creation-wrapper">
    <div class="survey-creation">
      <div class="caption">
        <p class="survey-title">Создать опрос</p>
        <button class="close" @click="closeQuiz"></button>
      </div>
      <input :value="quizData?.title" @change="changeTitle" type="text" class="survey-name"
             placeholder="Название опроса">
      <div class="survey-list">
        <div class="survey-element" v-for="(item, index) in quizData?.answers" :key="item?.order">
          <input :value="item?.body" @change="event => changeQuestion(event, index)" type="text" class="survey-input"
                 placeholder="Название пункта">
          <button class="survey-delete" @click="deleteQuestion(index)"></button>
        </div>
      </div>
      <button class="add-field" @click="addQuestion">Добавить еще поле</button>
      <button class="publish btn-purple" @click="createQuiz">Опубликовать опрос</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "SurveyCreation",
  props: ['quizData'],
  methods: {
    changeTitle(event) {
      this.$emit('changeTitle', event?.target?.value);
    },
    addQuestion() {
      this.$emit('addQuestion');
    },
    changeQuestion(event, index) {
      this.$emit('changeQuestion', {
        val: event?.target?.value, index
      });
    },
    deleteQuestion(index) {
      this.$emit('deleteQuestion', index);
    },
    createQuiz() {
      this.$emit('createQuiz');
    },
    closeQuiz() {
      this.$emit('closeQuiz');
    }
  }
}
</script>

