<template>
  <div class="event-files-container">
    <a-list :grid="{ gutter: 16, xs: 1, sm: 2, md: 2, lg: 3, xl: 4, xxl: 4, xxxl:5 }" :data-source="eventFiles">
      <template #renderItem="{ item,index }">
        <a-list-item>
          <a-card :title="item?.title ? item.title : 'Без названия'" hoverable style="width: 300px">
            <template #extra>{{ item?.sideData?.size ? convertFileSize(item?.sideData?.size) : '' }}</template>

            <template #cover>
              <div class="" style="display: flex;justify-content: center">
                <img
                    class="file-icon"
                    v-if="changeFileIndex!==index"
                    alt="example"
                    src="@/assets/events-docs/file.svg"
                />
              </div>

              <div class="edit-file-container" v-if="changeFileIndex===index">
                <div class="edit-file-el">
                  <a-typography-title :level="5">
                    Название файла:
                  </a-typography-title>
                  <a-input placeholder="Название файла" v-model:value="currentFile.title"></a-input>
                </div>

                <div class="edit-file-el">
                  <a-button @click="$refs?.eventFileUploader?.click()" type="primary">Загрузить файл
                    <template #icon>
                      <upload-outlined></upload-outlined>
                    </template>
                  </a-button>
                </div>

              </div>
            </template>
            <template #actions>
              <a-popover trigger="hover">
                <download-outlined @click="downloadDocument(item)" key="setting"/>
                <template #content>
                  <p>Скачать</p>
                </template>
              </a-popover>
              <a-popover trigger="hover" v-if="changeFileIndex!==index">
                <edit-outlined @click="changeFileIndex = index" key="edit"/>
                <template #content>
                  <p>Редактировать</p>
                </template>
              </a-popover>
              <a-popover v-if="changeFileIndex===index" trigger="hover">
                <save-outlined style="color: #0fcbcc" @click="saveDocument"
                               key="edit"/>
                <template #content>
                  <p>Сохранить</p>
                </template>
              </a-popover>
              <a-popover v-if="changeFileIndex===index" trigger="hover">
                <delete-outlined style="color: red" @click="deleteDocument"
                                 key="edit"/>
                <template #content>
                  <p>Удалить</p>
                </template>
              </a-popover>


            </template>
            <!--            <a-card-meta :title="item.title" description="This is the description">-->
            <!--              <template #avatar>-->
            <!--                <a-avatar src="https://joeschmoe.io/api/v1/random" />-->
            <!--              </template>-->
            <!--            </a-card-meta>-->
          </a-card>
        </a-list-item>
      </template>

    </a-list>
    <div class="" style="display: flex; justify-content: center">
      <a-button @click="createNewFile">Добавить файл
        <template #icon>
          <plus-outlined></plus-outlined>
        </template>
      </a-button>
      <input ref="eventFileUploader" type="file" @change="uploadFile" hidden="">
    </div>
  </div>
</template>

<script>
import {defineComponent} from 'vue'
import {
  EditOutlined,
  DownloadOutlined,
  PlusOutlined,
  DeleteOutlined,
  SaveOutlined,
  UploadOutlined
} from "@ant-design/icons-vue";
import {convertFileSize} from "@/lib/convertTime";

export default defineComponent({
  name: "EventFiles",
  components: {
    EditOutlined,
    DownloadOutlined, PlusOutlined,
    DeleteOutlined, SaveOutlined, UploadOutlined
  },
  props: ['eventId'],
  computed: {
    currentFile: {
      get() {
        if (this.changeFileIndex != null && this.changeFileIndex > -1) {
          return this.eventFiles[this.changeFileIndex];
        }
        return null;
      },
      set(val) {
        this.eventFiles[this.changeFileIndex] = val;
      }

    }
  },
  data() {
    return {
      eventFiles: [],
      changeFileIndex: null
    }
  },
  methods: {
    convertFileSize,
    fetchEventFiles() {
      this.$store.dispatch('fetchEventFiles', this.eventId).then((data) => {
        this.eventFiles = data.map(item => {
          item.sideData = JSON.parse(item.sideData);
          return item;
        });
      });
    },
    createNewFile() {
      const newFile = {
        title: null,
        sideData: {},
        docUrl: null
      }
      this.eventFiles.push(newFile);
      this.changeFileIndex = this.eventFiles.length - 1;
    },
    uploadFile(event) {
      const file = event?.target?.files[event?.target?.files?.length - 1];
      this.eventFiles[this.changeFileIndex]['file'] = file;
      this.eventFiles[this.changeFileIndex].sideData = {
        size: file?.size,
        type: file?.type
      }
    },
    downloadDocument(doc) {
      const el = document.createElement('a');
      el.href = doc.docUrl;
      el.download = (doc.title ? doc.title : 'Без названия');
      el.click();
    },
    async saveDocument() {
      if (!this.currentFile) return;
      if (this.currentFile?.file) {
        await this.$store.dispatch('loadFile', this.currentFile.file).then(url => {
          this.currentFile.docUrl = url;
          delete this.currentFile.file;
        })
      }
      this.$store.dispatch('saveEventDocument', {
        eventId: this.eventId,
        eventDocument: {...this.currentFile}
      }).then(this.fetchEventFiles);
      this.changeFileIndex = null;

    },
    deleteDocument() {
      if (!this.currentFile) return;
      this.currentFile['delete'] = true;
      delete this.currentFile?.file;
      this.$store.dispatch('saveEventDocument', {
        eventId: this.eventId,
        eventDocument: {...this.currentFile}
      }).then(this.fetchEventFiles);
      this.changeFileIndex = null;
    }
  },


  beforeMount() {
    this.fetchEventFiles();
  }
})
</script>


<style scoped>

.edit-file-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 5px 15px;
}

.edit-file-el {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.file-icon {
  width: 100px;
  height: 120px;
}


</style>