<template>
  <div class="modal">
    <div class="review">
      <p class="review-title">Спасибо что были с нами!</p>
      <p class="review-text">
        Поставьте оценку спикеру
      </p>
      <div class="stars">
        <button class="star " :class="{'star-selected':estimation>=1}" @click="estimation=1"></button>
        <button class="star" :class="{'star-selected':estimation>=2}" @click="estimation=2"></button>
        <button class="star " :class="{'star-selected':estimation>=3}" @click="estimation=3"></button>
        <button class="star " :class="{'star-selected':estimation>=4}" @click="estimation=4"></button>
        <button class="star " :class="{'star-selected':estimation>=5}" @click="estimation=5"></button>
      </div>
      <p class="review-subtitle">Ваш отзыв</p>
      <textarea v-model="body" placeholder="Оставьте свой отзыв тут" class="review-textarea"></textarea>
      <button class="send" @click="saveReview">Оценить</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ReviewModal",
  props: ['exitRoute', 'translationId', 'haveReview'],
  data() {
    return {
      estimation: 0,
      body: null
    }
  },
  methods: {
    saveReview() {
      this.$store.dispatch('saveStreamReview', {
        estimation: this.estimation,
        body: this.body,
        translationId: this.translationId
      }).then(() => {
        this.$emit('saveReview')
        if (this.exitRoute) {
          this.$router.push(this.exitRoute);
        }
        this.$emit('closeModal');
      });
    }
  }
}
</script>

