<template>
  <div class="event-program-container">
    <quill-editor ref="editor" class="editor"></quill-editor>
  </div>
</template>


<script>
import {defineComponent} from 'vue'
import {QuillEditor} from "@vueup/vue-quill";
import '@vueup/vue-quill/dist/vue-quill.snow.css';


export default defineComponent({
  name: "EventProgram",
  components: {
    QuillEditor
  },
  props: ['eventId'],

  data() {
    return {
      eventProgram: null
    }
  },
  methods: {
    saveEventProgram() {
      this.eventProgram.progBody = this.$refs?.editor?.getHTML();
      this.eventProgram.sideData = (typeof this.eventProgram.sideData === 'string' ? this.eventProgram.sideData : JSON.stringify(this.eventProgram.sideData));
      this.$store.dispatch('saveEventProgram', {eventId: this.eventId, data: this.eventProgram}).then((status) => {
        if (status === 200) {
          this.$emit('closeWindow');
        }
      });
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchEventProgram', this.eventId).then((data) => {
      if (data?.length) {
        this.eventProgram = data[0];
        if (this.eventProgram?.progBody) {
          this.$refs?.editor?.pasteHTML(this.eventProgram?.progBody);
        }
        if (this.eventProgram?.sideData) {
          this.eventProgram.sideData = JSON.parse(this.eventProgram.sideData);
        }
      } else {
        this.eventProgram = {
          id: null,  // nullable
          progBody: null, // stringified html
          sideData: {},  // stringified json
        }
      }
    });
  }
})
</script>

<style scoped>
.editor {
  width: 100%;
  min-height: 350px;
}
</style>