import {defaultHttpResHandler} from "@/store/helpers/defaultHttpResHandler";
import {makeDeleteReq, makeGetReq, makePostReq} from "@/store/helpers/request";
import {appConfig} from "@/lib/appConfig";


export const fetchStreamById = async (token, id) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation/${id}`, {
    token
}))

export const fetchStreams = async (token) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation`, {
    token
}))


export const saveStream = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/translation`, data, {
    token,
    data: true
}));


export const deleteStream = async (token, data) => await defaultHttpResHandler(async () => await makeDeleteReq(`${appConfig.domain}/stream`, data, {
    token,
    data: true
}))


export const passingQuiz = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/quiz`, data, {
    token,
    data: true
}))


export const fetchQuizResults = async (token, quizId) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/quiz/${quizId}`, {
    token
}))


export const fetchStreamUsersList = async (token, streamId, page, limit) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation/${streamId}/users?page=${page}&limit=${limit}`, {
    token
}))


export const saveStreamReview = async (token, data) => await defaultHttpResHandler(async () => await makePostReq(`${appConfig.domain}/translation_review`, data, {
    token,
    data: true
}))


export const fetchStreamReviews = async (token, translationId, page, limit) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/translation_review/${translationId}?page=${page}&limit=${limit}`, {
    token,
}))



export const fetchQuizResultsList = async (token, translationId) => await defaultHttpResHandler(async () => await makeGetReq(`${appConfig.domain}/quiz_all/${translationId}`, {
    token,
}))








