import {
    fetchQuizResults, fetchQuizResultsList,
    fetchStreamById, fetchStreamReviews,
    fetchStreams, fetchStreamUsersList, passingQuiz,
    saveStream, saveStreamReview,
} from "@/store/stream/serverInterationMethods";
import {errorHandler} from "@/lib/errorHandler";

export const stream = {
    actions: {

        async fetchQuizResultsList({getters}, translationId) {
            const {data} = await fetchQuizResultsList(getters.token, translationId);
            return data;
        },

        async fetchStreamReviews({getters, commit}, {translationId, page, limit}) {
            const {data} = await fetchStreamReviews(getters.token, translationId, page, limit);
            commit('setReviewsListData', data);
        },

        async saveStreamReview({getters}, data) {
            await saveStreamReview(getters.token, data);
        },

        async fetchStreamUsersList({getters}, {streamId, page, limit}) {
            const {data} = await fetchStreamUsersList(getters.token, streamId, page, limit);
            return data
        },

        async passingQuiz({getters}, quizData) {
            const {data} = await passingQuiz(getters.token, quizData);
            return data;
        },

        async fetchQuizResults({getters}, quizId) {
            const {data} = await fetchQuizResults(getters.token, quizId);
            return data;
        },

        async fetchStreams({getters, commit}) {
            const {data} = await fetchStreams(getters.token);
            commit('setStreamsList', data);
        },

        async fetchStreamById({getters, commit}, id) {
            let data;

            if (id === 'new') {
                data = {
                    title: null,
                    body: null,
                    description: null,

                }
            } else {
                const response = await fetchStreamById(getters.token, id);
                data = response.data;
            }
            commit('setStreamData', Array.isArray(data) ? data[0] : data);
        },
        async saveStream({getters}) {
            const stream = {
                ...getters.stream
            };
            stream.sideData = JSON.stringify(stream.sideData);
            const {status} = await saveStream(getters.token, stream);
            errorHandler({name: 'streamEdit', status});
        },
        async fetchLastStream({getters, commit}) {
            const {data} = await fetchStreamById(getters.token, 'last');
            commit('setStreamData', Array.isArray(data) ? data[0] : data);
            commit('setViewStreamModal', true);
        },
    },
    mutations: {

        setReviewsListData(state, data) {
            state.reviewsListData = {
                total: data?.reviewsCount,
                reviews: data.reviews
            }
        },
        clearReviewsList(state) {
            state.reviewsListData = null;
        },

        setStreamData(state, data) {
            state.stream = data;
            if (state.stream?.sideData) {
                state.stream.sideData = JSON.parse(state.stream?.sideData);
            }
        },
        changeStreamData(state, {field, val}) {
            state.stream[field] = val;
        },
        changeStreamSideData(state, {field, val}) {
            if (!state.stream?.sideData) {
                state.stream.sideData = {};
            }
            state.stream.sideData[field] = val;
        },
        setStreamsList(state, data) {
            state.streamsList = data;
        },
        setViewStreamModal(state, data) {
            state.viewStreamModal = data;
        }
    },
    state: {
        stream: null,
        streamsList: [],
        viewStreamModal: false,
        reviewsListData: null
    },
    getters: {
        stream: (state) => state.stream,
        streamsList: state => state.streamsList,
        viewStreamModal: state => state.viewStreamModal,
        reviewsListData: state => state.reviewsListData

    }
}