<template>
  <div class="event-news-container">
    <div class="editor-filling" v-for="(item, index) in eventNews" :key="item?.id">
      <div class="editor-info-block">
        <div class="editor-info-block-el">
          <a-typography-title style="max-width: 100px; width: 100%" :level="5">Название:
          </a-typography-title>
          <a-input v-if="currentNewsIndex === index" placeholder="Название мероприятия"
            v-model:value="currentNews.title"></a-input>
          <a-typography-text v-else>{{ item?.title ? item.title : 'Без названия' }}</a-typography-text>
        </div>
        <div class="editor-info-block-el">
          <a-typography-title style="max-width: 100px; width: 100%" :level="5">Описание:
          </a-typography-title>
          <a-textarea v-if="currentNewsIndex === index" :rows="6" placeholder="Описание мероприятия"
            v-model:value="currentNews.body"></a-textarea>
          <a-typography-text v-else>{{ item?.body ? item.body : 'Без описания' }}</a-typography-text>
        </div>

        <div class="editor-info-block-el">
          <a-button v-if="currentNewsIndex !== index" @click="currentNewsIndex = index">Редактировать</a-button>
          <a-button v-if="currentNewsIndex === index" type="primary" @click="saveEventNews">Сохранить</a-button>
          <a-button v-if="currentNewsIndex === index" danger @click="deleteEventNews">Удалить</a-button>
        </div>
      </div>
      <div class="preview-block">
        <img class="preview-photo" :src="item?.previewUrl && item?.previewUrl !== 'null'
          ? item?.previewUrl
          : require('@/assets/placeholder-image.png')
          " alt="" />
        <div class="photo-btn" v-if="currentNewsIndex === index">
          <a-button shape="round" @click="$refs.preview[currentNewsIndex].click()">
            <template #icon>
              <upload-outlined />
            </template>
            Загрузить фотографию
          </a-button>
          <p class="photo-description">Допустимое соотношение 16:9</p>
        </div>

        <input hidden type="file" ref="preview" @change="uploadPreview" />
      </div>


    </div>
  </div>
  <div class="" style="display: flex; justify-content: center">
    <a-button @click="addNews">Добавить</a-button>
  </div>
</template>


<script>
import { defineComponent } from 'vue'
import { UploadOutlined } from "@ant-design/icons-vue";

export default defineComponent({
  name: "EventNews",
  components: { UploadOutlined },
  props: ['eventId'],
  computed: {
    currentNews() {
      return this.currentNewsIndex != null && this.eventNews?.length ? this.eventNews[this.currentNewsIndex] : null;
    }
  },

  data() {
    return {
      eventNews: [],
      currentNewsIndex: null,
    }
  },
  methods: {
    async saveEventNews() {
      if (this.currentNews?.file) {
        this.currentNews.previewUrl = await this.$store.dispatch('loadFile', this.currentNews.file);
        delete this.currentNews.file;
      }

      this.$store.dispatch('saveEventNews', { eventId: this.eventId, newsData: this.currentNews })?.then((status) => {
        if (status === 200) {
          this.currentNewsIndex = null;
          this.fetchEventNews();
        }
      })
    },
    deleteEventNews() {
      if (this.currentNews?.id) {
        this.currentNews['delete'] = true;
        this.saveEventNews();
      } else {
        this.eventNews?.splice(this.currentNewsIndex, 1);
        this.currentNewsIndex = null;
      }
    },


    fetchEventNews() {
      this.$store.dispatch('fetchEventNews', this.eventId).then((data) => {
        if (data?.length) {
          this.eventNews = data;
        } else {
          this.eventNews = []
        }
      });
    },

    addNews() {
      this.eventNews?.push({
        id: null,  // nullable
        title: null,
        body: null,
        previewUrl: null,
        sideData: null
      });
      this.currentNewsIndex = this.eventNews.length - 1;
    },
    uploadPreview(event) {
      const file = event?.target?.files[event?.target?.files?.length - 1];
      this.currentNews.previewUrl = URL.createObjectURL(file);
      this.currentNews['file'] = file;
    }
  },
  beforeMount() {
    this.fetchEventNews();
  }
})
</script>

