  display: flex;
  flex-direction: column;
  gap: 15px;
}

.preview-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.preview-photo {
  max-width: 700px;
  width: 100%;
  height: 270px;
  object-fit: cover;
  object-position: center;
}

.photo-btn {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
}

.editor-filling {
  display: flex;
  width: 100%;
  gap: 10px;
}

.editor-info-block {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 70%;
}

.editor-info-block-el {
  display: flex;
  gap: 8px;
}

@media (max-width: 700px) {
  .editor-info-block {
    width: 100%;
  }

  .editor-filling {
    flex-direction: column;
  }
}
</style>