<template>
  <div class="answer-container">

    <div class="answer-element" v-if="type==='single'||type==='select'">
      <a-radio :checked="answer.correct" @change="$store.commit('setCorrectAnswer',{qIndex,aIndex,type})">
        <a-input placeholder="Ответ" :value="answer.body"
                 @change="e=>$store.commit('setAnswerBody',{qIndex,aIndex,val:e.target.value})"></a-input>
      </a-radio>
    </div>
    <div class="answer-element" v-if="type==='multi'||type==='multi-open'">
      <a-checkbox :checked="answer.correct||type==='multi-open'"
                  @change="$store.commit('setCorrectAnswer',{qIndex,aIndex,type})">
        <a-input placeholder="Ответ" :value="answer.body"
                 @change="e=>$store.commit('setAnswerBody',{qIndex,aIndex,val:e.target.value})"></a-input>
      </a-checkbox>
    </div>
    <div class="moving-buttons">
      <a-button @click="swapAnswers(aIndex,aIndex-1)" :disabled="!aIndex">
        <template #icon>
          <caret-up-outlined></caret-up-outlined>
        </template>
      </a-button>
      <a-button @click="swapAnswers(aIndex,aIndex+1)" :disabled="aIndex===answersLength-1">
        <template #icon>
          <caret-down-outlined></caret-down-outlined>
        </template>
      </a-button>
    </div>

    <a-button v-if="!answer.id" shape="round" @click="$store.commit('deleteAnswer',{qIndex,aIndex})" danger ghost
              size="small">
      <template #icon>
        <close-outlined/>
      </template>
    </a-button>

  </div>

</template>

<script>
import {CloseOutlined, CaretUpOutlined, CaretDownOutlined} from "@ant-design/icons-vue";

export default {
  name: "TestingAnswer",
  components: {
    CloseOutlined,
    CaretUpOutlined, CaretDownOutlined
  },
  props: ['answer', 'qIndex', 'aIndex', 'type', 'answersLength'],
  methods: {
    swapAnswers(from, to) {
      this.$store.commit('swapAnswers', {to, from, qIndex: this.qIndex});
    }
  }
}
</script>

<style scoped>


.answer-container {
  display: flex;
}

.answer-element {
  display: flex;
  flex-direction: column;
}

.moving-buttons {
  display: flex;
  gap: 5px;
  margin-right: 5px;
}


</style>