                  Результаты опросов
                </button>
                <!--                <button class="complete" disabled v-if="user?.role === 'admin'">{{-->
                <!--                  }}-->
                <!--                </button>-->

              </div>
            </div>
          </div>
        </div>
        <div class="caption-element chat-caption info-caption mobile">
          <p class="caption-name">Чат трансляции</p>
        </div>
        <div class="info">
          <SurveyCreation @closeQuiz="closeQuiz" @changeTitle="changeQuizTitle" @addQuestion="addQuizQuestion"
                          @changeQuestion="changeQuizQuestion" @deleteQuestion="deleteQuizQuestion"
                          @createQuiz="createQuiz"
                          :quiz-data="quizData" v-if="quizData">
          </SurveyCreation>
          <SurveyActive @closeQuiz="closePassingQuiz" @passingQuiz="passingQuiz" v-if="quizList.length"
                        :quiz-list="quizList">
          </SurveyActive>
          <div class="chat-wrapper">

            <div id="chat" class="chat" @scroll="scrollChat">

              <SeparateMessage v-for="(item, index) in messages" :key="item?.id" :id="`message${index + 1}`"
                               :moderator="streamModerator?.nickName" :speaker="streamSpeaker?.nickName"
                               :message="item">
              </SeparateMessage>

            </div>
          </div>

          <form class="form">
            <label class="field">
              <textarea id="newMessage" v-model="newMessage" @keydown.enter.prevent.exact="sendMessage"
                        @keydown.shift.enter.exact="newMessage += '\n'" class="field-textarea"
                        placeholder="Напишите сообщение"></textarea>
              <div class="emoji-wrapper">
                <EmojiPicker @select="(emoji) => newMessage+=emoji?.i" v-if="emojiOpen"/>
                <button type="button" @click="emojiOpen = !emojiOpen" class="emoji">
                  <img src="@/assets/emoji.svg" alt="" class="emoji-img">
                </button>
