    </div>

    <ReviewModal v-if="viewReviewForm" @closeModal="viewReviewForm = false" :exit-route="exitRoute"
                 :translation-id="this.streamId" @saveReview="haveReview = true">
    </ReviewModal>

  </div>
</template>

<script>
import EmojiPicker from 'vue3-emoji-picker'
import 'vue3-emoji-picker/css'
import SeparateMessage from '@/components/stream/SeparateMessage.vue'
import SeparateUser from '@/components/stream/SeparateUser.vue'
import SurveyCreation from '@/components/stream/SurveyCreation.vue'
import ReviewModal from '@/components/stream/ReviewModal.vue'
import SurveyActive from '@/components/stream/SurveyActive.vue'
import {appConfig} from "@/lib/appConfig";
import dayjs from "dayjs";
import locale from "dayjs/locale/ru"
import ReviewsTable from "@/components/stream/ReviewsTable.vue";
import QuizResults from "@/components/stream/QuizResults.vue";
import router from "@/router";

export default {
  name: "SeparateStreamPage",
  components: {
    QuizResults,
    ReviewsTable,
    EmojiPicker,
    SeparateMessage,
    SeparateUser,
    SurveyCreation,
    ReviewModal,
    SurveyActive
  },
  props: ['streamId', 'eventId'],
  computed: {
    stream() {
      return this.$store.getters.stream;
    },
    user() {
      return this.$store.getters.user;
    },
    currentStage() {
      const index = this.$store.getters.currentEvents?.stages?.map(item => item?.translationId).indexOf(this.streamId);
      return index > -1 ? this.$store.getters.currentEvents?.stages[index] : null;
    }
  },
  watch: {
    "openUsersList"(val) {
      if (!val) {
        this.usersPagination.page = 1;
        this.usersPagination.canLoadMore = true;
        this.usersList = [];
      } else {
        this.fetchUsersList();
      }
    },
    "messages.length"(val) {
      if (this.messagesPagination.page > 1 && document.getElementById('chat')?.scrollTop < document.getElementById('chat')?.clientHeight - 200) {
        return;
      }
      setTimeout(() => {
        document.getElementById('chat').scrollTo({
          top: document.getElementById(`message${val}`)?.offsetTop,
          behavior: 'smooth'
        });
      }, 100)
    },
    "messagesPagination.page"() {
      this.fetchMessages();
    },
    "usersPagination.page"() {
      this.fetchUsersList();
    }
  },

  data() {
    return {
      socket: null,


      messagesPagination: {
        page: 1,
        limit: 25,
        canLoadMore: true
      },
      messages: [],
      newMessage: '',
      emojiOpen: false,


      quizList: [],
      quizData: null,


      usersOnline: 0,
      streamSpeaker: null,
      streamModerator: null,


      openUsersList: false,
      usersList: [],
      usersPagination: {
        page: 1,
        limit: 10,
        canLoadMore: true
      },


      haveReview: false,
      viewReviewForm: false,


      viewReviewsModal: false,
      viewQuizesResultsModal: false,


      exitRoute: null,


      streamMetaDataTimer: null,
    }
  },
  methods: {
    router() {
      return router
    },
    dayjs,
    fetchStreamMetaData() {
      this.socket?.send(JSON.stringify({
        action: 'TRANSLATION_GET_METADATA',
        translationId: this.streamId,
        token: this.$store.getters.token
      }))
    },
    scrollChat(event) {
      if (!this.messagesPagination.canLoadMore) return;
      if (event?.target?.scrollTop === 0) {
        this.messagesPagination.page++;
      }
    },
    loadMoreUsers() {
      if (!this.usersPagination.canLoadMore) return;
      this.usersPagination.page++;
    },
    fetchUsersList() {
      this.$store.dispatch('fetchStreamUsersList', {
        streamId: this.streamId,
        page: this.usersPagination.page,
        limit: this.usersPagination.limit
      }).then(data => {
        if (data) {
          this.usersList = [...this.usersList, ...data];
          this.usersPagination.canLoadMore = data.length === this.usersPagination.limit;
        } else {
          this.usersPagination.canLoadMore = false;
        }
      });
    },
    fetchMessages() {
      this.socket?.send(JSON.stringify({
        action: "GET_TRANSLATION_MESSAGES",
        token: this.$store.getters.token,
        translationId: this.streamId,
        page: this.messagesPagination.page,
        limit: this.messagesPagination.limit
      }));
    },
    sendMessage() {
      if (!this.newMessage?.trim()) return;
      this.emojiOpen = false;
      this.socket?.send(JSON.stringify({
        action: 'SEND_MESSAGE',
        body: this.newMessage?.trim(),
        token: this.$store.getters.token,
        translationId: this.streamId
      }));
      this.newMessage = '';
    },
    editQuiz() {
      this.quizData = this.quizData ? null : {
        title: null,
        answers: [],
        passing: false
      }
    },
    changeQuizTitle(val) {
      this.quizData.title = val;
    },
    addQuizQuestion() {
      this.quizData?.answers?.push({
        body: null,
        order: this.quizData?.answers?.length
      })
    },
    changeQuizQuestion({val, index}) {
      this.quizData.answers[index].body = val;
    },
    deleteQuizQuestion(index) {
      this.quizData?.answers?.splice(index, 1);
    },
    createQuiz() {
      if (!this.quizData || !this.quizData?.answers?.length || this.quizData?.answers?.map(item => !!item.body?.trim()).includes(false)) return;
      this.socket?.send(JSON.stringify({
        action: "CREATE_QUIZ",
        translationId: this.streamId,
        token: this.$store.getters.token,
        title: this.quizData?.title,
        answers: JSON.stringify(this.quizData?.answers.sort((a, b) => a?.order - b?.order))
      }));
      this.quizData = null;
    },
    passingQuiz({quizId, answerOrder}) {
      this.$store.dispatch('passingQuiz', {quizId: quizId, answerOrder}).then((results) => {
        const index = this.quizList.map(item => item.id).indexOf(quizId);
        if (index > -1) {
          this.quizList[index]['results'] = results;
          this.quizList[index]['passed'] = true;
        }
      });
    },
    closePassingQuiz() {
      this.quizList.pop();
    },
    closeQuiz() {
      this.quizData = null;
    },

    exitFromStream() {
      this.$router.push({name: 'separateEvent', params: {eventId: this.eventId}});
    },
    pickChat(nickName) {
      document?.getElementById('newMessage')?.focus();
      this.newMessage = `${nickName ? nickName : ''} `;
    },
    updateQuizResults(quizes) {
      for (let i = 0; i < quizes.length; i++) {
        for (let j = 0; j < this.quizList.length; j++) {
          if (quizes[i].id === this.quizList[j].id && this.quizList[j].passed) {
            this.quizList[j].results = quizes[i].answers;
            break;
          }
        }
      }
    },

    closeSocket() {
      clearInterval(this.streamMetaDataTimer);
      if (this.socket?.readyState > 1) return;
      this.socket?.send(JSON.stringify({
        action: `TRANSLATION_EXIT`,
        token: this.$store.getters.token,
        translationId: this.streamId
      }));
      this.socket?.close();
    }
  },
  beforeMount() {
    this.$store.dispatch('fetchStreamById', this.streamId).then(() => {
      this.streamModerator = {
        name: this.stream?.sideData?.moderatorName,
        nickName: this.stream?.sideData?.moderatorNickName
      }
      this.streamSpeaker = {
        name: this.stream?.sideData?.speakerName,
        nickName: this.stream?.sideData?.speakerNickName
      }


      this.socket = new WebSocket(appConfig.socket);
      this.socket.onopen = () => {
        this.socket?.send(JSON.stringify({
          action: 'TRANSLATION_JOIN',
          token: this.$store.getters.token,
          translationId: this.streamId,
        }));
      }
      this.socket.onmessage = (msg) => {
        const data = JSON.parse(msg.data);
        // if (data?.translationId !== this.streamId) return;
        switch (data.action) {

          case "TRANSLATION_INFO":
            this.usersOnline = data?.msg?.usersOnline;
            this.haveReview = data?.msg?.haveReview;
            this.fetchMessages();
            this.streamMetaDataTimer = setInterval(() => {
              this.fetchStreamMetaData();
            }, 20000);

            break;

          case "TRANSLATION_MESSAGES":
            if (data?.msg?.messages?.length < this.messagesPagination.limit) this.messagesPagination.canLoadMore = false;
            if (this.messagesPagination.page === 1) {
              this.messages = data?.msg?.messages?.reverse();
            } else {
              this.messages = [...data?.msg?.messages?.reverse(), ...this.messages,];
            }
            break;

          case "NEW_MESSAGE":
            this.messages.push(data?.msg)
            break;

          case "NEW_QUIZ":
            this.quizList.push({
              id: data?.msg?.id,
              title: data?.msg?.title,
              answers: JSON.parse(data?.msg?.answers)?.sort((a, b) => a.answerOrder - b.answerOrder),
            });
            break;

          case 'TRANSLATION_STATS':
            this.usersOnline = data?.msg?.usersOnline;
            this.updateQuizResults(data?.msg?.quizes);
            break;

        }
      }
    });
    this.$store.dispatch('fetchCurrentEvents', this.eventId);
    this.$store.dispatch('fetchQuizResultsList', this.streamId).then((data) => {
      this.quizList = data?.map(item => {
        item.answers = JSON.parse(item.answers);
        return item
      });
    });
    dayjs.locale(locale);
    window.onbeforeunload = (event) => {
      this.closeSocket();
      event.preventDefault();
      event.returnValue = ''
      return false;
    }
  },
  beforeUnmount() {
    this.closeSocket();
  },
  beforeRouteLeave(to) {
    if (this.exitRoute) return;
    this.closeSocket();
    if (!this.haveReview) {
      this.exitRoute = to;
      this.viewReviewForm = true;
      return false;
    }
  },
  unmounted() {
    window.onbeforeunload = null;
  }
};
</script>

