<template>
  <div class="event-container" v-if="currentEvents">


    <div class="info">
      <div class="info-content">
        <img :src="currentEvents.previewUrl" alt="" class="photo">
        <div class="info-description">
          <div class="info-description-element">
            <TeamOutlined style="color: #F97F27"/>
            <p class="info-description-text">{{ usersList?.length }} {{
                usersList?.length % 10 === 1 ? 'участник' : usersList?.length % 10 < 5 ? 'участника' : 'участников'
              }}</p>
          </div>
          <div class="info-description-element">
            <CalendarOutlined style="color: #F97F27"/>
            <p class="info-description-text"> {{
                new Date(currentEvents.startDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                ) +
                " - " +
                new Date(currentEvents.endDate).toLocaleDateString(
                    "ru-RU",
                    dateOptions
                )
              }}</p>
          </div>
          <div class="info-description-element" v-if="additionalData?.place">
            <environment-outlined style="color: #F97F27"/>
            <p class="info-description-text">{{ additionalData?.place }}</p>

          </div>
          <div class="info-description-element" v-if="additionalData?.target">
            <img src="@/assets/portfolio-icon.svg" alt="" class="icon">
            <p class="info-description-text">{{ additionalData?.target }}</p>

          </div>
        </div>
        <p class="title">
          {{ currentEvents.title }}
        </p>
        <div class="filling">
          <p class="text">
            {{ currentEvents.description }}
          </p>
          <div class="info-block" v-if="eventProgram && (role==='admin' || eventPassing?.passingStatus ==='passed')"
               v-html="eventProgram?.progBody">
          </div>
          <!--          <div class="info-block">-->
          <!--            <p class="info-block-title">Заголовок 1</p>-->
          <!--            <p class="text">-->
          <!--              Подзаголовок. Мероприятие отлично подойдет для тех кто давно хотел научится настраивать рекламу в Facebook-->
          <!--              Ad-->
          <!--              Manager</p>-->
          <!--            <ul class="list">-->
          <!--              <li class="list-element">Lorem ipsum dolor sit amet.</li>-->
          <!--              <li class="list-element">Lorem ipsum dolor sit amet.</li>-->
          <!--              <li class="list-element">Lorem ipsum dolor sit amet.</li>-->
          <!--              <li class="list-element">Lorem ipsum dolor sit amet.</li>-->
          <!--              <li class="list-element">Lorem ipsum dolor sit amet.</li>-->
          <!--            </ul>-->
          <!--          </div>-->
        </div>
        <div class="materials" v-if=" eventFiles.length && (role==='admin' || eventPassing?.passingStatus ==='passed')">
          <p class="materials-title">Материалы мероприятия</p>
          <div class="materials-list">
            <div class="materials-element" v-for="item in eventFiles" :key="item?.id">
              <img src="@/assets/events-docs/file.svg" alt="" class="materials-icon">
              <p class="materials-text">{{
                  item?.title
                }}</p>
              <div class="materials-end">
                <a :href="item?.docUrl" :download="item?.title ? item.title : 'Без названия'"
                   class="materials-link">Скачать</a>
                <p class="materials-end-text">{{
                    item?.sideData?.size ? convertFileSize(item?.sideData?.size) : ''
                  }}</p>
              </div>
            </div>

          </div>
        </div>
      </div>
      <div class="participants" v-if="role==='admin' || eventPassing?.passingStatus ==='passed'">
        <p class="participants-title">Участники</p>
        <div class="participants-list">
          <SeparateUser v-for="item in usersList?.slice(0, 4)" :key="item?.id" :user="item">
          </SeparateUser>
        </div>
        <div class="list-btn-wrapper">
          <button class="list-btn" :disabled="disabeldViewUserListButton" @click="openUsersList = !openUsersList">Список
            участников >>
          </button>
          <div class="users" :class="{ 'users__open': openUsersList }">
            <div class="users-caption">
              <p class="users-title">Список участников</p>
              <button class="close" @click="openUsersList = !openUsersList">
                <img src="@/assets/close.svg" alt="Закрыть" class="close-img">
              </button>
            </div>
            <div class="users-list">
              <SeparateUser v-for="item in usersList" :key="item?.id" :user="item">
              </SeparateUser>
            </div>
            <button class="users-close btn-purple" @click="openUsersList = !openUsersList">Закрыть</button>
          </div>
        </div>
      </div>
      <div class="participants news"
           v-if="eventNews?.length && (role==='admin' || eventPassing?.passingStatus ==='passed')">
        <p class="participants-title news-title">Новости мероприятия</p>
        <div class="news__list">
          <NewsElement v-for="item in eventNews" :key="item.id" :news="item">
          </NewsElement>
        </div>
      </div>
    </div>

    <a-button shape="round" @click="viewStatistic = true" v-if="role === 'admin'" style="display: none">Статистика
      прохождения
    </a-button>
    <div class="plug">
      <div class="steps-caption" @click="stepsOpen = !stepsOpen">
        <button class="steps-caption-btn" :class="{ 'steps-caption-btn__open': stepsOpen }"></button>
      </div>
    </div>
    <div class="steps-block" :class="{ 'steps-block__open': stepsOpen }">
      <div class="steps-caption">

      </div>
      <a-timeline>
        <div class="time-line-element" v-for="  item   in   currentEvents.stages.sort((a, b) => a.order - b.order)  "
             :key="item.id" :class="{
            'event-disabled': new Date(item.startDate) > new Date(),
            'event-process':
              new Date(item.startDate) < new Date() &&
              new Date() < new Date(item.endDate) &&
              getStagePrecent(item) !== 100,
            'event-sucses':
              new Date() > new Date(item.endDate) ||
              getStagePrecent(item) === 100,
          }
            ">
          <a-timeline-item style="margin-left: auto">
            <div class="time-line-filling">
              <p class="date">
                {{
                  new Date(item.startDate).toLocaleDateString(
                      "ru-RU",
                      dateOptions
                  ) +
                  " - " +
                  new Date(item.endDate).toLocaleDateString(
                      "ru-RU",
                      dateOptions
                  )
                }}
              </p>
              <div class="timeline-title">
                <fund-outlined style="color: #3F1C9D; font-size: 20px" v-if="item.type === 'test'"/>
                <video-camera-outlined style="color: #3F1C9D; font-size: 20px" v-if="item.type === 'interview'"/>
                <schedule-outlined style="color: #3F1C9D; font-size: 20px" v-if="item.type === 'form'"/>
                <profile-outlined style="color: #3F1C9D; font-size: 20px" v-if="item.type === 'exercise'"/>
                <customer-service-outlined style="color: #3F1C9D; font-size: 20px" v-if="item.type === 'stream'"/>
                <a-typography-title :level="5">
                  {{
                    item.type === "test"
                        ? (item?.testing?.title ? `${item?.testing?.title} ` : 'Тестирование')
                        : item.type === "form"
                            ? (item?.testing?.title ? `${item?.testing?.title} ` : 'Анкета')
                            : item.type === "interview"
                                ? "Видео-интервью"
                                : item.type === "exercise"
                                    ? "Практическое задание"
                                    : item.type === "stream"
                                        ? "Трансляция"
                                        : null
                  }}
                </a-typography-title>

              </div>
              <a-typography-text v-if="item?.type === 'form' && getShortDescription(item)" type="secondary"
                                 class="text text-second">
                {{
                  textSymbolLimit(getShortDescription(item), viewMoreDescription === item.id ? getShortDescription()?.length
                      : descriptionSymbolLimit)
                }}
                <button style="font-weight: 500" v-if="getShortDescription(item)?.length > descriptionSymbolLimit"
                        @click="viewMoreDescription = (viewMoreDescription === item.id ? null : item.id)">
                  {{ viewMoreDescription === item.id ? 'Скрыть' : 'Показать больше' }}
                </button>
              </a-typography-text>

              <a-typography-text v-if="(item.type === 'exercise' || item.type === 'test') &&
                checkScore(item) === 'score'
                " type="secondary" class="text">
                Правильность прохождения:
                {{
                  Math.trunc(
                      (eventPassing.testingPassing[
                              eventPassing.testingPassing
                                  .map((pass) => pass.testingId)
                                  .indexOf(item.testId)
                              ].finalScore /
                          item.maxScore) *
                      100
                  )
                }}%
              </a-typography-text>
              <br/>

              <a-button :class="{
                    blocked: [
                      'Не началось',
                      'Пройдено',
                      'Ожидает проверки',
                      'Завершено',
                      'Не пройдено',
                      'Заполненo',
                    ].includes(getButtonText(item)),
                  }
                  " @click="goToStage(item)">
                {{ getButtonText(item) }}
              </a-button>
            </div>
            <template #dot>
              <a-progress :stroke-width="4" :format="() => item.order" :stroke-color="'#3F1C9D'" type="circle"
                          status="active" :percent="getStagePrecent(item)" :width="32"/>
            </template>
          </a-timeline-item>
        </div>

        <div class="time-line-element" :class="{
          'event-unapproved': eventPassing?.passingStatus !== 'passed',
          'event-sucses': eventPassing?.passingStatus === 'passed',
        }
          ">
          <a-timeline-item style="margin-left: auto">
            <template #dot>
              <a-progress :stroke-width="4" :stroke-color="eventPassing?.passingStatus === 'passed'
                ? '#3F1C9D'
                : '#808080'
                " type="circle" :percent="100" :width="32"/>
            </template>
          </a-timeline-item>
        </div>
      </a-timeline>
    </div>

  </div>

  <check-outlined v-if="false"/>
</template>

<script>
import {mapGetters} from "vuex";
import {
  // FieldTimeOutlined,
  // CheckCircleOutlined,
  // SyncOutlined,
  // MinusCircleOutlined,
  CheckOutlined,
  FundOutlined,
  VideoCameraOutlined,
  ScheduleOutlined,
  ProfileOutlined,
  CustomerServiceOutlined,
  CalendarOutlined,
  TeamOutlined,
  EnvironmentOutlined
} from "@ant-design/icons-vue";
import {errorHandler} from "@/lib/errorHandler";
import {textSymbolLimit} from "@/lib/textSymbolLimit";
import SeparateUser from '@/components/stream/SeparateUser.vue'
import {convertFileSize} from "../../lib/convertTime";
import NewsElement from "@/components/events/NewsElement.vue";
// import StatisticWindow from "@/components/events/StatisticWindow";

export default {
  name: "SeparateEventsPage",
  props: ["eventId"],
  components: {
    // StatisticWindow,
    // CheckCircleOutlined,
    // SyncOutlined,
    // MinusCircleOutlined,
    // FieldTimeOutlined,
    CheckOutlined,
    FundOutlined,
    VideoCameraOutlined,
    ScheduleOutlined,
    ProfileOutlined,
    CustomerServiceOutlined,
    SeparateUser,
    TeamOutlined,
    CalendarOutlined,
    EnvironmentOutlined,
    NewsElement
  },
  computed: {
    ...mapGetters(["currentEvents", "eventPassing", "role", "stream"]),
    additionalData() {
      if (this.currentEvents?.additionalData) {
        return JSON.parse(this.currentEvents?.additionalData);
      }
      return null;
    }
  },
  watch: {
    viewStream: function (val) {
      if (!val) {
        this.$store.commit("setStreamData", null);
      }
    },
    // openUsersList(val) {
    //   if (val) {
    //     const stageId = this.currentEvents?.stages[0]?.id;
    //
    //     if (!stageId) return;
    //
    //     this.$store.dispatch('fetchUsersByStageId', {
    //       stageId
    //     }).then(data => {
    //       this.usersList = data;
    //     });
    //   } else {
    //     this.disabeldViewUserListButton = true;
    //     setTimeout(() => {
    //       this.disabeldViewUserListButton = false;
    //     }, 2000)
    //   }
    // }
  },
  beforeMount() {
    scroll(0, 0);
    this.$store
        .dispatch("fetchCurrentEvents", this.eventId)
        .then(async (data) => {
          if (this.role !== 'admin') {
            if (!data || new Date() > new Date(data.endDate)) {
              this.$router.push({name: "allEvents"});
              return;
            }
          }
          document.dispatchEvent(
              new CustomEvent("setTitle", {detail: data.title})
          );
          await this.$store.dispatch(
              "fetchEventPassing",
              data.eventPassings.length ? data.eventPassings[0].id : null
          );
          try {
            const eventProgram = await this.$store.dispatch('fetchEventProgram', this.eventId);
            this.eventProgram = eventProgram?.[0];
          } catch (e) {
            console.log(e);
          }

          try {
            this.eventFiles = await this.$store.dispatch('fetchEventFiles', this.eventId);
            this.eventFiles.map(item => {
              item.sideData = JSON.parse(item.sideData);
              return item
            })
          } catch (e) {
            console.log(e)
          }

          try {
            this.eventNews = await this.$store.dispatch('fetchEventNews', this.eventId);
          } catch (e) {
            console.log(e);
          }

          const stage = this.currentEvents?.stages[0];
          if (!stage) return;
          if (stage.type === 'interview') {
            this.$store.dispatch('fetchInterviewById', stage?.interviewId).then(() => {
              this.$store.getters.interview?.interviewStageSlots?.map(item => {
                for (let i = 0; i < item?.usersInSlots.length; i++) {
                  if (this.usersList.map(item => item.id).includes(item?.usersInSlots[i]?.client?.id)) {
                    continue;
                  }
                  this.usersList.push(item?.usersInSlots[i]?.client);
                }
              })
            });
          } else {
            this.$store.dispatch('fetchUsersByStageId', {
              stageId: stage?.id
            }).then(data => {
              if (data?.[0]?.testing?.testingPassing) {
                for (let i = 0; i < data?.[0]?.testing?.testingPassing?.length; i++) {
                  if (this.usersList.map(item => item.id).includes(data?.[0]?.testing?.testingPassing[i].eventPassing?.client?.id)) {
                    continue;
                  }
                  this.usersList.push(data?.[0]?.testing?.testingPassing[i].eventPassing?.client);
                }
              }
            });
          }
        });
  },
  data() {
    return {
      CheckOutlined,
      dateOptions: {
        month: "long",
        day: "numeric",
      },
      viewStatistic: false,
      openUsersList: false,
      stepsOpen: false,
      viewStream: false,
      descriptionSymbolLimit: 120,
      viewMoreDescription: null,

      eventFiles: [],
      eventProgram: null,

      eventNews: [],

      usersList: [],
      disabeldViewUserListButton: false
    };
  },
  methods: {
    convertFileSize,
    textSymbolLimit,
    // pickStream(stage) {
    //   this.$store.dispatch("fetchStreamById", stage.translationId).then(() => {
    //     this.viewStream = true;
    //   });
    // },
    getShortDescription(item) {
      try {
        const sideData = JSON.parse(item?.testing?.sideData);
        return sideData?.shortDescription;
      } catch (e) {
        return null;
      }
    },
    checkScore(item) {
      if (this.eventPassing) {
        if (item.type === "interview") {
          return "score";
        } else {
          let index = this.eventPassing.testingPassing
              .map((pass) => pass.testingId)
              .indexOf(item.testId);
          if (index !== -1) {
            if (this.eventPassing.testingPassing[index].finalScore != null) {
              return "score";
            } else {
              return "pass";
            }
          }
        }
      } else {
        return null;
      }
    },
    getButtonText(item) {
      if (new Date(item.startDate) > new Date()) {
        return "Не началось";
      }
      if (item.type === "form") {
        const index =
            this.eventPassing && this.eventPassing.testingPassing
                ? this.eventPassing.testingPassing
                    .map((pass) => pass.testingId)
                    .indexOf(item.testId)
                : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Заполненo";
          return "Заполнено";
        }
        return "Заполнить";
      }

      if (item.type === "test" || item.type === "exercise") {
        const index =
            this.eventPassing && this.eventPassing.testingPassing
                ? this.eventPassing.testingPassing
                    .map((pass) => pass.testingId)
                    .indexOf(item.testId)
                : -1;
        if (index !== -1) {
          if (this.eventPassing.testingPassing[index].finalScore != null)
            return "Пройдено";
          return "Ожидает проверки";
        }
        return "Пройти";
      }

      if (item.type === "interview") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }

        return "Календарь";
      }

      if (item.type === "stream") {
        if (new Date() > new Date(item.endDate)) {
          return "Завершено";
        }
        return "Смотреть";
      }

      if (new Date() > new Date(item.endDate)) {
        return "Не пройдено";
      }
      return "";
    },
    goToStage(stage) {
      let passingId = this.currentEvents.eventPassings.length
          ? this.currentEvents.eventPassings[0].id
          : "null";

      if (new Date() > new Date(stage.endDate)) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап закончился",
          },
        });
        return;
      }
      if (new Date(stage.startDate) > new Date()) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Этап не начался",
          },
        });
        return;
      }

      if (stage.type === "stream") {
        this.$router.push({name: 'streamPage', params: {streamId: stage?.translationId, eventId: this.eventId}})
        return;
      }

      if (
          (stage.type !== "interview" &&
              this.eventPassing &&
              this.eventPassing.testingPassing &&
              this.eventPassing.testingPassing
                  .map((pass) => pass.testingId)
                  .indexOf(stage.testId) !== -1 &&
              stage.type !== "form") ||
          (stage.type === "form" &&
              this.eventPassing &&
              this.eventPassing.testingPassing &&
              this.eventPassing.testingPassing
                  .map((pass) => pass.testingId)
                  .indexOf(stage.testId) !== -1 &&
              this.eventPassing.testingPassing[
                  this.eventPassing.testingPassing
                      .map((pass) => pass.testingId)
                      .indexOf(stage.testId)
                  ].finalScore != null)
      ) {
        errorHandler({
          name: "appointment",
          status: 300,
          meta: {
            body: "Вы уже прошли этот этап",
          },
        });
        return;
      }
      switch (stage.type) {
        case "interview":
          this.$router.push({
            name: "interview",
            params: {
              interviewId: stage.interviewId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "test":
          this.$router.push({
            name: "testing",
            params: {
              testingId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
              score: "null",
            },
          });
          break;
        case "form":
          this.$router.push({
            name: "form",
            params: {
              formId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
        case "exercise":
          this.$router.push({
            name: "exercise",
            params: {
              exerciseId: stage.testId,
              eventId: this.currentEvents.id,
              role: "user",
              passingId,
            },
          });
          break;
      }
    },
    getStagePrecent(item) {
      return item.type !== "interview" && this.checkScore(item) === "score"
          ? item.type === "form"
              ? this.checkScore(item) === "score"
                  ? 100
                  : 0
              : (this.eventPassing.testingPassing[
                      this.eventPassing.testingPassing
                          .map((pass) => pass.testingId)
                          .indexOf(item.testId)
                      ].finalScore /
                  item.maxScore) *
              100
          : 0;
    },
  },
  mounted() {
    if (document.getElementById("separateEventCSS")) return;
    const element = document.createElement("link");
    element.rel = "stylesheet";
    element.href = "/css/separateEvent.css";
    element.id = "separateEventCSS";
    document.body.append(element);
  },
  beforeUnmount() {
    document.getElementById("separateEventCSS")?.remove();
    this.$store.commit("setEventPassing", null);
  },
};
</script>

