<script>
import locale from "ant-design-vue/es/date-picker/locale/ru_RU";
import dayjs from "dayjs";
// import {IMaskComponent} from "vue-imask";
import {errorHandler} from "@/lib/errorHandler";

export default {
  name: "UserProfilePage",
  props: ['userId'],
  computed: {
    userData() {
      return this.$store.getters.currentUser;
    },
    userName() {
      return this.userData ? this.userData?.name?.split(' ') : null
    }
  },
  // components: {
  //   IMaskComponent,
  // },
  data() {
    return {
      locale,
      dayjs,
    };
  },

  methods: {
    saveNewPassword() {
      const msg = [];
      if (!this.changePassword.newPassword) {
        msg.push('новый пароль')
      }

      if (!this.changePassword.newPasswordConfirm) {
        msg.push('подтверждение пароля')
      }

      if (msg.length) {
        errorHandler({name: 'requiredField', msg: msg.join(',')});
        return;
      }

      if (this.changePassword.newPassword !== this.changePassword.newPasswordConfirm) {
        errorHandler({name: 'incorrectPasswordEnter'});
        return;
      }

      this.$store.dispatch('changePassword', {
        password: this.changePassword.newPassword
      }).then(() => {
        this.viewChangePassword = false;
      })

    },
    splitName() {
      this.userData.name = this.userData.name.split(" ");
      if (this.userData.name.length < 3) {
        for (let i = 0; i < 3 - this.userData.name.length; i++) {
          this.userData.name.push("");
        }
      }
    },
    loadFile() {
      let el = this.$refs.file;
      this.file = el.files[el.files.length - 1];
      this.userData.avatarUrl = URL.createObjectURL(this.file);
    },
    async saveSettings() {
      if (this.file) {
        await this.$store
            .dispatch("loadFile", this.file)
            .then((url) => (this.userData.avatarUrl = url));
      }
      let user = JSON.parse(JSON.stringify(this.userData));
      user.phone = this.unmaskedPhone;
      for (let i = 0; i < this.userData.name.length; i++) {
        this.userData.name[i] = this.userData.name[i].trim();
      }
      user.name = this.userData.name.join(" ");
      if (user.email && !/^[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/.test(user.email)) {
        errorHandler({name: 'incorrectEmail'})
        return;
      }
      this.$store.dispatch("updateUserData", user).then(() => {
        this.file = null;
        this.$store.dispatch("fetchUserData");
      });
    },
  },
  beforeMount() {
    scroll(0, 0);
    this.$store.dispatch('fetchUserById', this.userId);
    // this.splitName();
    // this.userData.birth = dayjs(
    //     this.userData.birth ? this.userData.birth : new Date()
    // );
  }
};
</script>

